/**
 * Defines the contents of the tooltip. See CSS for tooltip styling. The tooltip
 * features the country name, population, GDP, and CO2 emissions, preceded
 * by a label and followed by units where applicable.
 *
 * @param {object} d The data associated to the hovered element
 * @returns {string} The tooltip contents
 */
export function getContents (d) {
  // TODO : Generate tooltip contents
  return `
  <span>Country : </span>       <span style="font-weight:normal">${d['Country Name']}</span>
  <br>
  <span>Population : </span>    <span style="font-weight:normal">${d['Population']}</span>
  <br>
  <span>GDP : </span>           <span style="font-weight:normal">${d['GDP'].toFixed(2)} $ (USD)</span>
  <br>
  <span>CO2 emissions : </span> <span style="font-weight:normal">${d['CO2'].toFixed(2)} metric tonnes</span>
  `
}
